.content_wrapper .list_wrapper ul {
    display: -ms-flexbox;
    display: flex;
    list-style-type: none;
    -ms-flex-pack: center;
        justify-content: center;
}

.content_wrapper .list_wrapper ul div {
    display: -ms-flexbox;
    display: flex;
    width: 90%;
    height: 100%;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
    -ms-flex-pack: distribute;
        justify-content: space-around;
}

.content_wrapper .list_wrapper ul li {
    display: -ms-flexbox;
    display: flex;
    margin: 20px;
    width: 250px;
    height: 250px;
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;
}

.content_wrapper .list_wrapper ul li img {
    position: relative;
    width: 170px;
    height: 200px;
}

.content_wrapper .list_wrapper ul li:hover {
    cursor: pointer;
    background-color: rgba(0, 0, 0, 0.7)
}

.content_wrapper .list_wrapper ul li p {
    word-break:break-word;
}

.content_wrapper .list_wrapper ul .clear {
    clear: both;
}

.content_wrapper .load_button {
    padding: 50px;
    background-color: #222;
}

.content_wrapper .load_button button {
    width: 100px;
    height: 30px;
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    background-color: grey;
    border: none;
    font-weight: bolder;
}

.content_wrapper .load_button button:hover {
    background-color: rgba(128, 128, 128, 0.5);
}



